@import url(https://fonts.googleapis.com/css?family=Reenie+Beanie&display=swap);
body, html, #root {
    width: 100%;
    
    margin: 0;
    padding: 0;
}

html, #root {
    overflow-x: hidden;

    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    letter-spacing: 2px;
    color: #333;
}

.text-unselectable {
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    
    -webkit-user-select: none;  
    -moz-user-select: none;    
    -ms-user-select: none;      
    user-select: none;
}

.clickable {
    cursor: pointer;
}

clickable:focus {
    outline:none;
}

a {
    text-decoration: none;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 

    -webkit-animation: color-change 10000ms infinite;
    animation: color-change 10000ms infinite;
}

@-webkit-keyframes color-change {
    0% { color: #2196F3; }
    25% { color: #4CAF50; }
    50% { color: #FFC107; }
    75% { color: #FF5722; }
    100% { color: #2196F3; }
}

@keyframes color-change {
    0% { color: #2196F3; }
    25% { color: #4CAF50; }
    50% { color: #FFC107; }
    75% { color: #FF5722; }
    100% { color: #2196F3; }
}
/* desktop-x-small */
@media all and (min-width: 0) and (max-width: 480px) {
    .navbar-dimensions {
        height: 56px;
        width: 100%;
    }

    .navbar-item-dimensions {
        margin: 4px;
        padding-top: 16px;
        padding-bottom: 16px;
        padding-left: 8px;
        padding-right: 8px;
    }

    .navbar-logo {
        display: none;
    }
}

/* desktop-small */
@media all and (min-width: 481px) and (max-width: 840px) {
    .navbar-dimensions {
        height: 56px;
        width: 100%;
    }

    .navbar-item-dimensions {
        margin: 8px;
        padding-top: 16px;
        padding-bottom: 16px;
        padding-left: 8px;
        padding-right: 8px;
    }

    .navbar-logo {
        margin: 20px;
        padding: 16px;
    
        font-size: 18px;
    
        color: #333;
        border: 2px dashed #f69c55;
    }
}

/* desktop-medium */
@media all and (min-width: 841px) {
    .navbar-dimensions {
        height: 88px;
        width: 100%;
    }

    .navbar-item-dimensions {
        margin: 8px;
        padding: 16px;
    }

    .navbar-logo {
        margin: 20px;
        padding: 16px;
    
        font-size: 18px;
    
        color: #333;
        border: 2px dashed #f69c55;
    }
}

.navbar {
    position: fixed;

    z-index: 100;

    display: flex;
    justify-content: center; /* align horizontal */
    align-items: center; /* align vertical */

    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 700;
    line-height: 1em;
    color: #333;

    -webkit-transition: all 300ms ease-in-out;
    transition: all 300ms ease-in-out;
}

.elevated {
    background-color: white;
    box-shadow: 0 3px 6px rgba(0,0,0,0.10);
}

.navbar-item {
    border-radius: 4px;

    -webkit-transition: all 200ms ease-in-out;
    transition: all 200ms ease-in-out;
}

.navbar-item:hover, .navbar-logo:hover {
    -webkit-transition: all 200ms ease-in;
    transition: all 200ms ease-in;

    background-color: rgba(0, 0, 0, 0.05);
}

.navbar-item-selected {
    border-radius: 4px;

    -webkit-animation: navbar-item-background-change 10000ms infinite;
    animation: navbar-item-background-change 10000ms infinite;
}

@-webkit-keyframes navbar-item-background-change {
    0% { background-color: rgba(33, 150, 243, 0.2); }
    25% { background-color: rgba(76, 175, 80, 0.2); }
    50% { background-color: rgba(255, 193, 7, 0.2); }
    75% { background-color: rgba(255, 87, 34, 0.2); }
    100% { background-color: rgba(33, 150, 243, 0.2); }
}

@keyframes navbar-item-background-change {
    0% { background-color: rgba(33, 150, 243, 0.2); }
    25% { background-color: rgba(76, 175, 80, 0.2); }
    50% { background-color: rgba(255, 193, 7, 0.2); }
    75% { background-color: rgba(255, 87, 34, 0.2); }
    100% { background-color: rgba(33, 150, 243, 0.2); }
}
.profile-picture-container {
  width:250px;
  height:100%;
  overflow:hidden;
}

.profile-picture-container img {
  object-fit: cover;
  width:300px;
}

@media all and (max-width : 840px) {  
  .cracked {
    font-size: 3.5em;
  }
}

@media all and (min-width : 841px) {  
  .cracked {
    font-size: 5em;
  }
}

.cracked {
  background-color: black;
  font-family: Coolvetica, Arial;
  color:#fff;
  text-transform: uppercase;
  position: relative;
  -webkit-animation: glow 3s linear infinite 5s;
          animation: glow 3s linear infinite 5s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  text-shadow: 0 0 3rem #ff2525;
  -webkit-filter: brightness(150%) blur(0.07rem);
          filter: brightness(150%) blur(0.07rem);
}
.cracked span:nth-child(1) ,
.cracked span:nth-child(3) {
  position: absolute;
  top:0;
  left:0;
}
.cracked span:nth-child(1)  {
  -webkit-clip-path: polygon(-10% 0%, 10% 0%, 60% 100%, -10% 100%);
          clip-path: polygon(-10% 0%, 10% 0%, 60% 100%, -10% 100%);
  -webkit-animation: crack1 0.2s linear forwards 2s;
          animation: crack1 0.2s linear forwards 2s;
}
.cracked span:nth-child(2)  {
  -webkit-clip-path: polygon(10% 0%, 40% 0%, 80% 100%, 60% 100%);
          clip-path: polygon(10% 0%, 40% 0%, 80% 100%, 60% 100%);
}
.cracked span:nth-child(3) {
  -webkit-clip-path: polygon(40% 0%, 110% 0%, 110% 100%, 80% 100%);
          clip-path: polygon(40% 0%, 110% 0%, 110% 100%, 80% 100%);
  -webkit-animation: crack2 0.2s linear forwards 4s;
          animation: crack2 0.2s linear forwards 4s;
}
@-webkit-keyframes crack1 {
  0% {-webkit-transform: translate(0%,0%);transform: translate(0%,0%); }
  100% {-webkit-transform: translate(-2%, 1%) rotateZ(1deg);transform: translate(-2%, 1%) rotateZ(1deg); }
}
@keyframes crack1 {
  0% {-webkit-transform: translate(0%,0%);transform: translate(0%,0%); }
  100% {-webkit-transform: translate(-2%, 1%) rotateZ(1deg);transform: translate(-2%, 1%) rotateZ(1deg); }
}
@-webkit-keyframes crack2 {
  0% {-webkit-transform: translate(0%,0%);transform: translate(0%,0%); }
  100% {-webkit-transform: translate(2%, -1%) rotateZ(1deg);transform: translate(2%, -1%) rotateZ(1deg); }
}
@keyframes crack2 {
  0% {-webkit-transform: translate(0%,0%);transform: translate(0%,0%); }
  100% {-webkit-transform: translate(2%, -1%) rotateZ(1deg);transform: translate(2%, -1%) rotateZ(1deg); }
}
@-webkit-keyframes glow {
  0% {
    text-shadow: 0 0 3rem #ff2525;
    -webkit-filter: brightness(150%) blur(0.07rem);
            filter: brightness(150%) blur(0.07rem);
  }
  100% {
    text-shadow: 0 0 3rem #c98686;
    -webkit-filter: brightness(150%) blur(0.12rem);
            filter: brightness(150%) blur(0.12rem);
  }
}
@keyframes glow {
  0% {
    text-shadow: 0 0 3rem #ff2525;
    -webkit-filter: brightness(150%) blur(0.07rem);
            filter: brightness(150%) blur(0.07rem);
  }
  100% {
    text-shadow: 0 0 3rem #c98686;
    -webkit-filter: brightness(150%) blur(0.12rem);
            filter: brightness(150%) blur(0.12rem);
  }
}



.root-home {
    height: 100%;
    width: 100%;

    overflow-y: auto;
    overflow-x: hidden;

    margin: 0;
    padding: 0;
}

.work {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.route-fade-enter {
    opacity: 0;
}

.route-fade-enter.route-fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 600ms ease-in-out;
  transition: opacity 600ms ease-in-out;
}

.route-fade-exit {
    opacity: 1;
    position: fixed;
    display: none
}

.route-fade-exit.route-fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 0ms ease-in-out;
  transition: opacity 0ms ease-in-out;
}
